/**
 * Enhanced Navigation Components - 2025 Edition
 * 
 * Modern navigation with mobile-first design, accessibility features,
 * and smooth interactions.
 * 
 * Features:
 * - Mobile-first responsive design
 * - Hamburger menu with smooth animations
 * - Touch-friendly targets and gestures
 * - Enhanced accessibility and keyboard navigation
 * - Scroll-based styling changes
 * - Modern glassmorphism effects
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   MAIN HEADER
   ========================================================================== */

.main-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: var(--z-index-sticky);
  background: rgba(10, 10, 26, 0.9);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: var(--transition-all);
}

.main-header.scrolled {
  background: rgba(10, 10, 26, 0.95);
  box-shadow: var(--shadow-lg);
  border-bottom-color: rgba(0, 255, 224, 0.2);
}

/* ==========================================================================
   NAVBAR
   ========================================================================== */

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-4) var(--spacing-5);
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

/* ==========================================================================
   BRAND
   ========================================================================== */

.navbar-brand {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  text-decoration: none;
  color: var(--cosmic-white);
  font-family: var(--font-display);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  transition: var(--transition-colors);
  z-index: var(--z-index-docked);
}

.navbar-brand:hover {
  color: var(--accent-color);
}

.navbar-logo {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-lg);
  transition: var(--transition-transform);
}

.navbar-brand:hover .navbar-logo {
  transform: scale(1.05) rotate(5deg);
}

.brand-text {
  background: var(--gradient-text);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* ==========================================================================
   NAVIGATION LINKS
   ========================================================================== */

.nav-links {
  display: flex;
  align-items: center;
  gap: var(--spacing-6);
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-links a {
  color: var(--cosmic-white);
  text-decoration: none;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-base);
  padding: var(--spacing-2) var(--spacing-3);
  border-radius: var(--radius-md);
  position: relative;
  transition: var(--transition-colors);
  min-height: 44px;
  display: flex;
  align-items: center;
}

.nav-links a::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--gradient-accent);
  transform: translateX(-50%);
  transition: width var(--duration-300) var(--ease-smooth);
}

.nav-links a:hover {
  color: var(--accent-color);
}

.nav-links a:hover::before {
  width: 100%;
}

/* ==========================================================================
   MOBILE MENU TOGGLE
   ========================================================================== */

.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: var(--spacing-2);
  border-radius: var(--radius-md);
  transition: var(--transition-colors);
  z-index: var(--z-index-docked);
}

.mobile-menu-toggle:hover {
  background: rgba(255, 255, 255, 0.1);
}

.hamburger-line {
  width: 24px;
  height: 2px;
  background: var(--cosmic-white);
  margin: 2px 0;
  transition: var(--transition-all);
  border-radius: var(--radius-sm);
}

/* Hamburger animation */
.mobile-menu-toggle.active .hamburger-line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
  background: var(--accent-color);
}

.mobile-menu-toggle.active .hamburger-line:nth-child(2) {
  opacity: 0;
}

.mobile-menu-toggle.active .hamburger-line:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
  background: var(--accent-color);
}

/* ==========================================================================
   MOBILE NAVIGATION
   ========================================================================== */

@media (max-width: 768px) {
  .mobile-menu-toggle {
    display: flex;
  }
  
  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    width: 280px;
    height: 100vh;
    background: rgba(10, 10, 26, 0.98);
    backdrop-filter: blur(20px);
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    padding: var(--spacing-20) var(--spacing-6) var(--spacing-6);
    gap: var(--spacing-2);
    transition: right var(--duration-300) var(--ease-smooth);
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: var(--shadow-2xl);
  }
  
  .nav-links.mobile-open {
    right: 0;
  }
  
  .nav-links a {
    width: 100%;
    padding: var(--spacing-4);
    font-size: var(--font-size-lg);
    border-radius: var(--radius-lg);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    justify-content: center;
    text-align: center;
  }
  
  .nav-links a:last-child {
    border-bottom: none;
  }
  
  .nav-links a::before {
    display: none;
  }
  
  .nav-links a:hover {
    background: rgba(0, 255, 224, 0.1);
    border-color: var(--accent-color);
  }
  
  /* Mobile menu overlay */
  .nav-links.mobile-open::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    z-index: -1;
  }
}

/* ==========================================================================
   TABLET RESPONSIVE
   ========================================================================== */

@media (max-width: 1024px) and (min-width: 769px) {
  .navbar {
    padding: var(--spacing-3) var(--spacing-4);
  }
  
  .nav-links {
    gap: var(--spacing-4);
  }
  
  .nav-links a {
    font-size: var(--font-size-sm);
    padding: var(--spacing-2);
  }
}

/* ==========================================================================
   TOUCH ENHANCEMENTS
   ========================================================================== */

@media (pointer: coarse) {
  .nav-links a {
    min-height: 48px;
    padding: var(--spacing-3) var(--spacing-4);
  }
  
  .mobile-menu-toggle {
    min-height: 48px;
    min-width: 48px;
  }
  
  .navbar-brand {
    min-height: 48px;
  }
}

/* ==========================================================================
   GESTURE SUPPORT
   ========================================================================== */

@media (max-width: 768px) {
  /* Swipe gesture hint */
  .nav-links.mobile-open {
    position: relative;
  }
  
  .nav-links.mobile-open::after {
    content: '';
    position: absolute;
    top: 50%;
    left: -20px;
    width: 4px;
    height: 40px;
    background: var(--accent-color);
    border-radius: var(--radius-full);
    transform: translateY(-50%);
    animation: swipeHint 2s ease-in-out infinite;
  }
}

@keyframes swipeHint {
  0%, 100% {
    opacity: 0.3;
    transform: translateY(-50%) translateX(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-50%) translateX(-5px);
  }
}

/* ==========================================================================
   ACCESSIBILITY ENHANCEMENTS
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .nav-links {
    transition: none;
  }
  
  .hamburger-line {
    transition: none;
  }
  
  .navbar-logo {
    transition: none;
  }
  
  .navbar-brand:hover .navbar-logo {
    transform: none;
  }
  
  .swipeHint {
    animation: none;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .main-header {
    background: var(--primary-color);
    border-bottom: 2px solid var(--cosmic-white);
  }
  
  .nav-links a {
    border: 1px solid transparent;
  }
  
  .nav-links a:hover {
    border-color: var(--accent-color);
    background: transparent;
  }
  
  .mobile-menu-toggle {
    border: 2px solid var(--cosmic-white);
  }
}

/* ==========================================================================
   BACK TO TOP BUTTON
   ========================================================================== */

.back-to-top {
  position: fixed;
  bottom: var(--spacing-6);
  right: var(--spacing-6);
  width: 56px;
  height: 56px;
  border-radius: var(--radius-full);
  background: var(--gradient-accent);
  color: var(--primary-color);
  border: none;
  cursor: pointer;
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  z-index: var(--z-index-docked);
  transition: var(--transition-all);
  box-shadow: var(--shadow-lg);
}

.back-to-top:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl), 0 0 20px rgba(0, 255, 224, 0.4);
}

.back-to-top:active {
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .back-to-top {
    bottom: var(--spacing-4);
    right: var(--spacing-4);
    width: 48px;
    height: 48px;
    font-size: var(--font-size-lg);
  }
}