/**
 * Enhanced Button Components - 2025 Edition
 * 
 * Modern button styles with improved accessibility, micro-interactions,
 * and contemporary design patterns.
 * 
 * Features:
 * - Multiple button variants (primary, secondary, ghost, etc.)
 * - Enhanced focus states and accessibility
 * - Smooth micro-interactions and hover effects
 * - Loading states and disabled states
 * - Responsive sizing and touch-friendly targets
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   BASE BUTTON STYLES
   ========================================================================== */

.btn {
  /* Layout and sizing */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-2);
  padding: var(--spacing-3) var(--spacing-6);
  min-height: 44px; /* Touch-friendly minimum */
  min-width: 44px;
  
  /* Typography */
  font-family: var(--font-primary);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  text-decoration: none;
  text-align: center;
  white-space: nowrap;
  
  /* Visual styling */
  border: 2px solid transparent;
  border-radius: var(--radius-lg);
  background: transparent;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  
  /* Transitions and animations */
  transition: var(--transition-all);
  transform: translateY(0);
  
  /* Accessibility */
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.btn:focus-visible {
  outline: none;
  box-shadow: var(--focus-ring), var(--focus-ring-offset);
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  pointer-events: none;
}

/* ==========================================================================
   BUTTON VARIANTS
   ========================================================================== */

/* Primary Button */
.btn-primary {
  background: var(--gradient-accent);
  color: var(--primary-color);
  border-color: var(--accent-color);
  font-weight: var(--font-weight-semibold);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg), 0 0 20px rgba(0, 255, 224, 0.3);
}

.btn-primary:active {
  transform: translateY(0);
}

/* Secondary Button */
.btn-secondary {
  background: transparent;
  color: var(--accent-color);
  border-color: var(--accent-color);
}

.btn-secondary:hover {
  background: var(--accent-color);
  color: var(--primary-color);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

/* Ghost Button */
.btn-ghost {
  background: transparent;
  color: var(--cosmic-white);
  border-color: transparent;
}

.btn-ghost:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-1px);
}

/* Danger Button */
.btn-danger {
  background: var(--error-color);
  color: var(--cosmic-white);
  border-color: var(--error-color);
}

.btn-danger:hover {
  background: #c82333;
  border-color: #c82333;
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

/* ==========================================================================
   BUTTON SIZES
   ========================================================================== */

.btn-sm {
  padding: var(--spacing-2) var(--spacing-4);
  font-size: var(--font-size-sm);
  min-height: 36px;
}

.btn-lg {
  padding: var(--spacing-4) var(--spacing-8);
  font-size: var(--font-size-lg);
  min-height: 52px;
}

.btn-xl {
  padding: var(--spacing-5) var(--spacing-10);
  font-size: var(--font-size-xl);
  min-height: 60px;
}

/* ==========================================================================
   BUTTON STATES
   ========================================================================== */

.btn-loading {
  position: relative;
  color: transparent;
}

.btn-loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

/* ==========================================================================
   SPECIAL BUTTON EFFECTS
   ========================================================================== */

/* Glow effect for hero buttons */
.btn-glow {
  position: relative;
}

.btn-glow::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-accent);
  border-radius: inherit;
  opacity: 0;
  transition: opacity var(--duration-300) var(--ease-smooth);
  z-index: -1;
  filter: blur(8px);
}

.btn-glow:hover::before {
  opacity: 0.7;
}

/* Ripple effect */
.btn-ripple {
  position: relative;
  overflow: hidden;
}

.btn-ripple::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width var(--duration-300) var(--ease-out), 
              height var(--duration-300) var(--ease-out);
}

.btn-ripple:active::after {
  width: 300px;
  height: 300px;
}

/* ==========================================================================
   ANIMATIONS
   ========================================================================== */

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* ==========================================================================
   RESPONSIVE DESIGN
   ========================================================================== */

@media (max-width: 768px) {
  .btn {
    padding: var(--spacing-3) var(--spacing-5);
    font-size: var(--font-size-sm);
  }
  
  .btn-lg {
    padding: var(--spacing-4) var(--spacing-6);
    font-size: var(--font-size-base);
  }
  
  .btn-xl {
    padding: var(--spacing-4) var(--spacing-8);
    font-size: var(--font-size-lg);
  }
}

/* ==========================================================================
   ACCESSIBILITY ENHANCEMENTS
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .btn {
    transition: none;
    transform: none;
  }
  
  .btn:hover {
    transform: none;
  }
  
  .btn-loading::after {
    animation: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .btn {
    border-width: 3px;
  }
  
  .btn-primary {
    background: var(--accent-color);
    color: var(--primary-color);
  }
  
  .btn-secondary {
    background: transparent;
    color: var(--cosmic-white);
    border-color: var(--cosmic-white);
  }
}