/**
 * Enhanced Footer Component Styles - 2025 Edition
 * 
 * Modern footer styling with cosmic theme, improved accessibility,
 * and responsive design for all devices.
 * 
 * Features:
 * - Cosmic background effects and animations
 * - Enhanced typography and visual hierarchy
 * - Responsive grid layout for different screen sizes
 * - Social media integration with hover effects
 * - Accessibility improvements with proper focus states
 * - Modern button styling for legal links
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   FOOTER CONTAINER
   ========================================================================== */

.footer {
  background: linear-gradient(135deg, 
    var(--primary-color) 0%, 
    var(--secondary-color) 50%, 
    var(--primary-color) 100%);
  color: var(--text-primary);
  padding: var(--spacing-8) 0 var(--spacing-6);
  position: relative;
  overflow: hidden;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.footer-cosmic-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

.footer-constellation {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(2px 2px at 20px 30px, var(--accent-color), transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(255, 255, 255, 0.3), transparent),
    radial-gradient(1px 1px at 90px 40px, var(--accent-color), transparent),
    radial-gradient(1px 1px at 130px 80px, rgba(255, 255, 255, 0.2), transparent);
  background-repeat: repeat;
  background-size: 200px 100px;
  animation: twinkle 4s ease-in-out infinite alternate;
}

.footer-stardust {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, 
    rgba(0, 255, 224, 0.1) 0%, 
    transparent 70%);
  animation: pulse 3s ease-in-out infinite;
}

@keyframes twinkle {
  0% { opacity: 0.3; }
  100% { opacity: 0.8; }
}

@keyframes pulse {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.6; }
}

.footer-container {
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 var(--spacing-4);
  position: relative;
  z-index: 2;
}

/* ==========================================================================
   FOOTER CONTENT GRID
   ========================================================================== */

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-8);
  margin-bottom: var(--spacing-8);
}

/* ==========================================================================
   FOOTER BRAND SECTION
   ========================================================================== */

.footer-brand {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  color: var(--text-primary);
}

.logo-icon {
  font-size: var(--font-size-2xl);
  filter: drop-shadow(0 0 10px var(--accent-color));
}

.logo-text {
  background: var(--gradient-cosmic);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: var(--font-heading);
}

.footer-tagline {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
  line-height: 1.6;
  margin: 0;
}

/* ==========================================================================
   FOOTER NAVIGATION
   ========================================================================== */

.footer-links {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
}

.footer-section-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin: 0 0 var(--spacing-3) 0;
  font-family: var(--font-heading);
}

.footer-nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2);
}

.footer-nav-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: var(--font-size-sm);
  transition: all var(--transition-normal);
  padding: var(--spacing-1) 0;
  position: relative;
}

.footer-nav-link:hover,
.footer-nav-link:focus {
  color: var(--accent-color);
  text-decoration: none;
  transform: translateX(4px);
}

.footer-nav-link::before {
  content: '';
  position: absolute;
  left: -var(--spacing-3);
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  height: 2px;
  background: var(--accent-color);
  transition: width var(--transition-normal);
}

.footer-nav-link:hover::before,
.footer-nav-link:focus::before {
  width: var(--spacing-2);
}

/* ==========================================================================
   CONTACT INFORMATION
   ========================================================================== */

.footer-contact {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
}

.footer-contact-info {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-3);
}

.contact-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
}

.contact-icon {
  font-size: var(--font-size-lg);
  filter: drop-shadow(0 0 5px var(--accent-color));
}

.contact-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: var(--font-size-sm);
  transition: all var(--transition-normal);
}

.contact-link:hover,
.contact-link:focus {
  color: var(--accent-color);
  text-decoration: underline;
}

.contact-text {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

/* ==========================================================================
   SOCIAL MEDIA LINKS
   ========================================================================== */

.footer-social {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
  text-align: center; /* Added for centering content */
}

.social-links {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-2);
  align-items: center; /* Added for centering links */
}

.social-link {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  color: var(--text-secondary);
  text-decoration: none;
  font-size: var(--font-size-sm);
  padding: var(--spacing-2);
  border-radius: var(--radius-md);
  transition: all var(--transition-normal);
  position: relative;
  overflow: hidden;
}

.social-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(0, 255, 224, 0.1), 
    transparent);
  transition: left var(--transition-normal);
}

.social-link:hover::before,
.social-link:focus::before {
  left: 100%;
}

.social-link:hover,
.social-link:focus {
  color: var(--accent-color);
  background: rgba(0, 255, 224, 0.05);
  transform: translateY(-2px);
}

.social-icon {
  font-size: var(--font-size-lg);
  transition: transform var(--transition-normal);
  filter: drop-shadow(0 0 5px var(--accent-color)); /* Added for glowing effect */
}

.social-link:hover .social-icon,
.social-link:focus .social-icon {
  transform: scale(1.1);
}

.social-name {
  font-weight: var(--font-weight-medium);
}

/* ==========================================================================
   FOOTER BOTTOM SECTION
   ========================================================================== */

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: var(--spacing-6);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  flex-wrap: wrap;
  gap: var(--spacing-4);
}

.footer-legal {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-3);
}

.copyright {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  margin: 0;
  flex-wrap: wrap;
}

.copyright-icon {
  color: var(--accent-color);
}

.copyright-year {
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
}

.copyright-company {
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  font-family: var(--font-heading);
}

.copyright-text {
  color: var(--text-muted);
}

.legal-links {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  flex-wrap: wrap;
}

.footer-legal-link {
  font-size: var(--font-size-sm);
  padding: var(--spacing-1) var(--spacing-3);
}

.legal-separator {
  color: var(--text-muted);
  font-size: var(--font-size-sm);
}

/* ==========================================================================
   FOOTER BADGE
   ========================================================================== */

.footer-badge {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  font-size: var(--font-size-xs);
  color: var(--text-muted);
  font-style: italic;
}

.badge-icon {
  font-size: var(--font-size-sm);
  filter: drop-shadow(0 0 5px var(--accent-color));
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* ==========================================================================
   RESPONSIVE DESIGN
   ========================================================================== */

@media (max-width: 768px) {
  .footer {
    padding: var(--spacing-6) 0 var(--spacing-4);
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: var(--spacing-6);
    text-align: center;
  }
  
  .footer-bottom {
    flex-direction: column;
    text-align: center;
    gap: var(--spacing-4);
  }
  
  .footer-legal {
    align-items: center;
  }
  
  .copyright {
    justify-content: center;
  }
  
  .legal-links {
    justify-content: center;
  }
  
  .social-links {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media (max-width: 480px) {
  .footer-container {
    padding: 0 var(--spacing-3);
  }
  
  .footer-content {
    gap: var(--spacing-4);
  }
  
  .copyright {
    flex-direction: column;
    gap: var(--spacing-1);
  }
  
  .legal-links {
    flex-direction: column;
    gap: var(--spacing-2);
  }
  
  .legal-separator {
    display: none;
  }
}

/* ==========================================================================
   ACCESSIBILITY ENHANCEMENTS
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .footer-constellation,
  .footer-stardust,
  .badge-icon {
    animation: none;
  }
  
  .footer-nav-link,
  .contact-link,
  .social-link,
  .social-icon {
    transition: none;
  }
}

@media (prefers-contrast: high) {
  .footer {
    border-top: 2px solid var(--text-primary);
  }
  
  .footer-nav-link,
  .contact-link,
  .social-link {
    border: 1px solid transparent;
  }
  
  .footer-nav-link:focus,
  .contact-link:focus,
  .social-link:focus {
    border-color: var(--accent-color);
    outline: 2px solid var(--accent-color);
    outline-offset: 2px;
  }
}

/* Focus visible support */
.footer-nav-link:focus-visible,
.contact-link:focus-visible,
.social-link:focus-visible,
.footer-legal-link:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
  border-radius: var(--radius-sm);
}