/**
 * Enhanced Card Components - 2025 Edition
 * 
 * Modern card designs with improved visual hierarchy, micro-interactions,
 * and accessibility features.
 * 
 * Features:
 * - Multiple card variants and sizes
 * - Hover effects and micro-interactions
 * - Enhanced accessibility with proper focus states
 * - Responsive design and mobile optimization
 * - Loading states and skeleton screens
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   BASE CARD STYLES
   ========================================================================== */

.card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-xl);
  padding: var(--spacing-6);
  position: relative;
  overflow: hidden;
  transition: var(--transition-all);
  
  /* Accessibility */
  outline: none;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl), 0 0 30px rgba(0, 255, 224, 0.1);
  border-color: rgba(0, 255, 224, 0.3);
}

.card:focus-within {
  border-color: var(--accent-color);
  box-shadow: var(--focus-ring), var(--focus-ring-offset);
}

/* ==========================================================================
   CARD VARIANTS
   ========================================================================== */

/* Service Card */
.card-service {
  text-align: center;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-service .card-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto var(--spacing-4);
  background: var(--gradient-accent);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-2xl);
  transition: var(--transition-transform);
}

.card-service:hover .card-icon {
  transform: scale(1.1) rotate(5deg);
}

/* Insight Card */
.card-insight {
  background: linear-gradient(135deg, rgba(0, 255, 224, 0.1) 0%, rgba(74, 144, 226, 0.1) 100%);
  border-left: 4px solid var(--accent-color);
}

.card-insight .card-metric {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  font-family: var(--font-display);
  line-height: 1;
  margin-bottom: var(--spacing-2);
}

/* Feature Card */
.card-feature {
  position: relative;
  overflow: hidden;
}

.card-feature::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-cosmic);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform var(--duration-300) var(--ease-smooth);
}

.card-feature:hover::before {
  transform: scaleX(1);
}

/* ==========================================================================
   CARD SIZES
   ========================================================================== */

.card-sm {
  padding: var(--spacing-4);
  border-radius: var(--radius-lg);
}

.card-lg {
  padding: var(--spacing-8);
  border-radius: var(--radius-2xl);
}

.card-xl {
  padding: var(--spacing-10);
  border-radius: var(--radius-2xl);
}

/* ==========================================================================
   CARD CONTENT
   ========================================================================== */

.card-header {
  margin-bottom: var(--spacing-4);
}

.card-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-2);
  font-family: var(--font-primary);
}

.card-subtitle {
  font-size: var(--font-size-sm);
  color: var(--cosmic-gray-400);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: var(--font-weight-medium);
}

.card-body {
  flex: 1;
  margin-bottom: var(--spacing-4);
}

.card-text {
  color: var(--cosmic-gray-100);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--spacing-4);
}

.card-footer {
  margin-top: auto;
  padding-top: var(--spacing-4);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* ==========================================================================
   CARD INTERACTIONS
   ========================================================================== */

/* Clickable cards */
.card-clickable {
  cursor: pointer;
  user-select: none;
}

.card-clickable:active {
  transform: translateY(-2px) scale(0.98);
}

/* Loading state */
.card-loading {
  position: relative;
  pointer-events: none;
}

.card-loading::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
}

/* Skeleton loading */
.card-skeleton {
  background: linear-gradient(90deg, 
    rgba(255, 255, 255, 0.05) 25%, 
    rgba(255, 255, 255, 0.1) 50%, 
    rgba(255, 255, 255, 0.05) 75%
  );
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s infinite;
}

@keyframes skeleton-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

/* ==========================================================================
   CARD GRID LAYOUTS
   ========================================================================== */

.card-grid {
  display: grid;
  gap: var(--spacing-6);
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.card-grid-2 {
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}

.card-grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

/* ==========================================================================
   RESPONSIVE DESIGN
   ========================================================================== */

@media (max-width: 768px) {
  .card {
    padding: var(--spacing-4);
    border-radius: var(--radius-lg);
  }
  
  .card-lg {
    padding: var(--spacing-6);
  }
  
  .card-xl {
    padding: var(--spacing-6);
  }
  
  .card-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-4);
  }
  
  .card-service {
    min-height: 250px;
  }
  
  .card-service .card-icon {
    width: 48px;
    height: 48px;
    font-size: var(--font-size-xl);
  }
}

/* ==========================================================================
   ACCESSIBILITY ENHANCEMENTS
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .card {
    transition: none;
  }
  
  .card:hover {
    transform: none;
  }
  
  .card-service:hover .card-icon {
    transform: none;
  }
  
  .card-skeleton {
    animation: none;
  }
}

@media (prefers-contrast: high) {
  .card {
    background: var(--primary-color);
    border: 2px solid var(--cosmic-white);
  }
  
  .card-insight {
    border-left-width: 6px;
  }
  
  .card-title {
    color: var(--cosmic-white);
  }
  
  .card-text {
    color: var(--cosmic-gray-100);
  }
}