/* Import enhanced global styles */
@import './styles/globals/variables.css';
@import './styles/globals/reset.css';
@import './styles/utilities/layout.css';
@import './styles/components/buttons.css';
@import './styles/components/cards.css';
@import './styles/components/navigation.css';
@import './styles/components/accessibility.css';
@import './styles/components/forms.css';
@import './styles/components/footer.css';

body {
  margin: 0;
  font-family: var(--font-secondary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-feature-settings: "kern" 1;
  background-color: var(--primary-color);
  color: var(--cosmic-white);
  perspective: 1px; /* For parallax effect */
}

code {
  font-family: var(--font-monospace);
}

/* Enhanced accessibility and motion preferences */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
