/**
 * Accessibility Enhancement Styles - 2025 Edition
 * 
 * Comprehensive accessibility styles following WCAG 2.1 AA guidelines
 * and modern accessibility best practices.
 * 
 * Features:
 * - Screen reader utilities
 * - Focus management and indicators
 * - Skip navigation links
 * - High contrast mode support
 * - Reduced motion preferences
 * - Touch-friendly targets
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   SCREEN READER UTILITIES
   ========================================================================== */

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  padding: inherit;
  margin: inherit;
  overflow: visible;
  clip: auto;
  white-space: normal;
}

/* ==========================================================================
   SKIP NAVIGATION
   ========================================================================== */

.skip-nav {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--accent-color);
  color: var(--primary-color);
  padding: var(--spacing-2) var(--spacing-4);
  text-decoration: none;
  border-radius: var(--radius-md);
  font-weight: var(--font-weight-semibold);
  z-index: var(--z-index-skipLink);
  transition: top var(--duration-200) var(--ease-smooth);
}

.skip-nav:focus {
  top: 6px;
}

/* ==========================================================================
   FOCUS MANAGEMENT
   ========================================================================== */

/* Enhanced focus indicators */
*:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
  border-radius: var(--radius-sm);
}

/* Remove default focus for mouse users */
*:focus:not(:focus-visible) {
  outline: none;
}

/* Focus trap for modals */
.focus-trap {
  position: relative;
}

.focus-trap::before,
.focus-trap::after {
  content: '';
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
  pointer-events: none;
}

/* ==========================================================================
   TOUCH AND INTERACTION TARGETS
   ========================================================================== */

/* Ensure minimum touch target size */
button,
input[type="button"],
input[type="submit"],
input[type="reset"],
a,
[role="button"],
[tabindex="0"] {
  min-height: 44px;
  min-width: 44px;
}

/* Touch-friendly spacing */
@media (pointer: coarse) {
  button,
  a,
  [role="button"] {
    padding: var(--spacing-3) var(--spacing-4);
  }
}

/* ==========================================================================
   HIGH CONTRAST MODE SUPPORT
   ========================================================================== */

@media (prefers-contrast: high) {
  /* Ensure sufficient contrast ratios */
  .btn-primary {
    background: var(--cosmic-white);
    color: var(--primary-color);
    border: 3px solid var(--primary-color);
  }
  
  .btn-secondary {
    background: transparent;
    color: var(--cosmic-white);
    border: 3px solid var(--cosmic-white);
  }
  
  /* Enhanced borders for better definition */
  .card,
  .form-group,
  .nav-links a {
    border: 2px solid currentColor;
  }
  
  /* Remove subtle effects that may not be visible */
  .hero-particles,
  .cosmic-glow,
  .hero-cosmic-glow {
    display: none;
  }
}

/* ==========================================================================
   REDUCED MOTION PREFERENCES
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  /* Disable animations and transitions */
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  /* Remove parallax effects */
  .hero-background-image {
    transform: none !important;
    animation: none !important;
  }
  
  /* Disable floating animations */
  .cosmic-orb,
  .hero-particles {
    animation: none !important;
  }
  
  /* Keep essential hover states but remove transforms */
  .btn:hover {
    transform: none !important;
  }
}

/* ==========================================================================
   KEYBOARD NAVIGATION ENHANCEMENTS
   ========================================================================== */

/* Visible focus for keyboard users */
.keyboard-user *:focus {
  outline: 3px solid var(--accent-color);
  outline-offset: 2px;
}

/* Enhanced navigation for keyboard users */
.nav-links a:focus,
.navbar-brand:focus {
  background: rgba(0, 255, 224, 0.1);
  border-radius: var(--radius-md);
}

/* Form focus enhancements */
input:focus,
textarea:focus,
select:focus {
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px rgba(0, 255, 224, 0.2);
}

/* ==========================================================================
   ARIA LIVE REGIONS
   ========================================================================== */

.live-region {
  position: absolute;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

/* Status messages */
.status-message {
  padding: var(--spacing-3);
  border-radius: var(--radius-md);
  margin: var(--spacing-2) 0;
}

.status-success {
  background: rgba(40, 167, 69, 0.1);
  border: 1px solid var(--success-color);
  color: var(--success-color);
}

.status-error {
  background: rgba(220, 53, 69, 0.1);
  border: 1px solid var(--error-color);
  color: var(--error-color);
}

.status-warning {
  background: rgba(255, 193, 7, 0.1);
  border: 1px solid var(--warning-color);
  color: var(--warning-color);
}

/* ==========================================================================
   MOBILE ACCESSIBILITY
   ========================================================================== */

@media (max-width: 768px) {
  /* Larger touch targets on mobile */
  .mobile-menu-toggle {
    min-height: 48px;
    min-width: 48px;
    padding: var(--spacing-3);
  }
  
  /* Better spacing for mobile navigation */
  .nav-links.mobile-open a {
    padding: var(--spacing-4);
    display: block;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  /* Enhanced form controls for mobile */
  input,
  textarea,
  select {
    min-height: 48px;
    font-size: 16px; /* Prevent zoom on iOS */
  }
}

/* ==========================================================================
   PRINT ACCESSIBILITY
   ========================================================================== */

@media print {
  /* Hide interactive elements */
  .mobile-menu-toggle,
  .back-to-top,
  .hero-particles,
  .cosmic-elements {
    display: none !important;
  }
  
  /* Ensure readable text */
  * {
    color: black !important;
    background: white !important;
  }
  
  /* Show URLs for links */
  a[href]:after {
    content: " (" attr(href) ")";
    font-size: smaller;
  }
}