/* ==========================================================================
   ENHANCED HERO SECTION
   ========================================================================== */

.hero-section {
  background: linear-gradient(135deg, var(--primary-color) 0%, #0a0a1a 50%, var(--secondary-color) 100%);
  color: var(--cosmic-white);
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
}

/* Enhanced background system */
.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.hero-background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./assets/hero-bg.webp');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transform: translateZ(-1px) scale(1.2);
  opacity: 0.3;
  animation: parallaxFloat 20s ease-in-out infinite;
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(2px 2px at 20px 30px, rgba(0, 255, 224, 0.8), transparent),
              radial-gradient(2px 2px at 40px 70px, rgba(74, 144, 226, 0.6), transparent),
              radial-gradient(1px 1px at 90px 40px, rgba(255, 255, 255, 0.9), transparent),
              radial-gradient(1px 1px at 130px 80px, rgba(0, 255, 224, 0.7), transparent),
              radial-gradient(2px 2px at 160px 30px, rgba(74, 144, 226, 0.5), transparent);
  background-repeat: repeat;
  background-size: 200px 100px;
  animation: particleFloat 25s linear infinite;
}

.hero-cosmic-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 800px;
  height: 800px;
  transform: translate(-50%, -50%);
  background: radial-gradient(circle, rgba(0, 255, 224, 0.1) 0%, rgba(74, 144, 226, 0.05) 50%, transparent 70%);
  animation: cosmicPulse 8s ease-in-out infinite;
}

/* Hero content container */
.hero-content {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  padding: 0 var(--spacing-5);
}

/* Enhanced logo styling */
.hero-logo-container {
  position: relative;
  display: inline-block;
  margin-bottom: var(--spacing-8);
}

.hero-logo {
  max-width: 350px;
  height: auto;
  filter: drop-shadow(0 0 20px rgba(0, 255, 224, 0.3));
  transition: all var(--duration-500) var(--ease-in-out);
}

.hero-logo:hover {
  filter: drop-shadow(0 0 30px rgba(0, 255, 224, 0.5));
  transform: scale(1.05);
}

.logo-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120%;
  height: 120%;
  transform: translate(-50%, -50%);
  background: radial-gradient(circle, rgba(0, 255, 224, 0.2) 0%, transparent 70%);
  animation: logoGlow 4s ease-in-out infinite;
  z-index: -1;
}

/* Enhanced title styling */
.hero-title {
  font-family: var(--font-primary);
  font-size: clamp(3rem, 8vw, 6rem);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--spacing-4);
  line-height: 1.1;
}

.title-main {
  background: linear-gradient(135deg, var(--cosmic-white) 0%, rgba(0, 255, 224, 0.9) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
}

.title-accent {
  background: linear-gradient(135deg, var(--highlight-color) 0%, rgba(255, 0, 209, 0.9) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  margin-left: var(--spacing-2);
  font-size: 0.8em;
  vertical-align: super;
}

/* Enhanced tagline */
.hero-tagline {
  font-family: var(--font-secondary);
  font-size: clamp(1.2rem, 3vw, 2rem);
  font-weight: var(--font-weight-medium);
  margin-bottom: var(--spacing-8);
  color: rgba(255, 255, 255, 0.9);
}

.tagline-highlight {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: var(--font-weight-bold);
}

/* Enhanced description */
.hero-description {
  max-width: 800px;
  margin: 0 auto var(--spacing-10);
}

.description-main {
  font-family: var(--font-secondary);
  font-size: clamp(1rem, 2.5vw, 1.3rem);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--spacing-4);
  color: rgba(255, 255, 255, 0.9);
}

.description-stats {
  font-family: var(--font-secondary);
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  color: rgba(255, 255, 255, 0.8);
  font-weight: var(--font-weight-medium);
}

.year-highlight {
  color: var(--highlight-color);
  font-weight: var(--font-weight-bold);
}

.text-gradient {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: var(--font-weight-semibold);
}

.stat-highlight {
  color: var(--accent-color);
  font-weight: var(--font-weight-bold);
  font-size: 1.1em;
}

/* Enhanced action buttons */
.hero-actions {
  display: flex;
  gap: var(--spacing-4);
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: var(--spacing-12);
}

.hero-button-primary {
  position: relative;
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  color: var(--cosmic-white);
  padding: var(--spacing-4) var(--spacing-8);
  text-decoration: none;
  border-radius: var(--radius-xl);
  font-family: var(--font-secondary);
  font-weight: var(--font-weight-semibold);
  font-size: 1.1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all var(--duration-300) var(--ease-in-out);
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 255, 224, 0.3);
}

.hero-button-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(0, 255, 224, 0.4);
}

.hero-button-primary span {
  position: relative;
  z-index: 2;
}

.button-glow {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left var(--duration-500) var(--ease-in-out);
}

.hero-button-primary:hover .button-glow {
  left: 100%;
}

.hero-button-secondary {
  background: transparent;
  color: var(--cosmic-white);
  padding: var(--spacing-4) var(--spacing-8);
  text-decoration: none;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: var(--radius-xl);
  font-family: var(--font-secondary);
  font-weight: var(--font-weight-medium);
  font-size: 1.1rem;
  transition: all var(--duration-300) var(--ease-in-out);
  backdrop-filter: blur(10px);
}

.hero-button-secondary:hover {
  border-color: var(--accent-color);
  background: rgba(0, 255, 224, 0.1);
  transform: translateY(-2px);
}

/* Scroll indicator */
.scroll-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-2);
  color: rgba(255, 255, 255, 0.6);
  animation: scrollBounce 2s ease-in-out infinite;
}

.scroll-arrow {
  width: 2px;
  height: 30px;
  background: linear-gradient(to bottom, transparent, var(--accent-color));
  position: relative;
}

.scroll-arrow::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid var(--accent-color);
}

.scroll-text {
  font-size: 0.8rem;
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Floating cosmic elements */
.cosmic-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.cosmic-orb {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(0, 255, 224, 0.3) 0%, transparent 70%);
  animation: orbFloat 15s ease-in-out infinite;
}

.orb-1 {
  width: 100px;
  height: 100px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.orb-2 {
  width: 60px;
  height: 60px;
  top: 60%;
  right: 15%;
  background: radial-gradient(circle, rgba(74, 144, 226, 0.3) 0%, transparent 70%);
  animation-delay: -5s;
}

.orb-3 {
  width: 80px;
  height: 80px;
  bottom: 30%;
  left: 20%;
  background: radial-gradient(circle, rgba(255, 0, 209, 0.2) 0%, transparent 70%);
  animation-delay: -10s;
}

/* Animations */
@keyframes parallaxFloat {
  0%, 100% { transform: translateZ(-1px) scale(1.2) translateY(0px); }
  50% { transform: translateZ(-1px) scale(1.2) translateY(-20px); }
}

@keyframes particleFloat {
  0% { transform: translateX(0px); }
  100% { transform: translateX(-200px); }
}

@keyframes cosmicPulse {
  0%, 100% { opacity: 0.3; transform: translate(-50%, -50%) scale(1); }
  50% { opacity: 0.6; transform: translate(-50%, -50%) scale(1.1); }
}

@keyframes logoGlow {
  0%, 100% { opacity: 0.5; transform: translate(-50%, -50%) scale(1); }
  50% { opacity: 0.8; transform: translate(-50%, -50%) scale(1.05); }
}

@keyframes scrollBounce {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(10px); }
}

@keyframes orbFloat {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  33% { transform: translateY(-30px) rotate(120deg); }
  66% { transform: translateY(20px) rotate(240deg); }
}

/* Fallback for browsers that don't support WebP */
@supports not (background-image: url('./assets/hero-bg.webp')) {
  .hero-background-image {
    background-image: url('./assets/hero-bg.jpg');
  }
}

/* Services section animations */
@keyframes gridFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes particlesDrift {
  0% { transform: translateX(0px) translateY(0px); }
  33% { transform: translateX(-20px) translateY(-10px); }
  66% { transform: translateX(10px) translateY(-20px); }
  100% { transform: translateX(0px) translateY(0px); }
}

@keyframes starTwinkle {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.7; transform: scale(1.2); }
}

@keyframes iconOrbit {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  100% { transform: translate(-50%, -50%) rotate(360deg); }
}

@keyframes ctaPulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.05); }
}

@keyframes iconFloat {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes constellationDrift {
  0% { transform: translateX(0px) translateY(0px); }
  25% { transform: translateX(10px) translateY(-5px); }
  50% { transform: translateX(-5px) translateY(10px); }
  75% { transform: translateX(-10px) translateY(-5px); }
  100% { transform: translateX(0px) translateY(0px); }
}

@keyframes stardustFloat {
  0%, 100% { transform: translateY(0px) scale(1); opacity: 0.7; }
  33% { transform: translateY(-15px) scale(1.1); opacity: 1; }
  66% { transform: translateY(10px) scale(0.9); opacity: 0.5; }
}

/* ==========================================================================
   ENHANCED ABOUT SECTION
   ========================================================================== */

.about-section {
  background: linear-gradient(135deg, var(--neutral-color) 0%, #1a1a2e 50%, var(--primary-color) 100%);
  padding: 120px 0;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}

/* Enhanced cosmic background system */
.about-cosmic-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.cosmic-constellation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(2px 2px at 100px 50px, rgba(74, 144, 226, 0.8), transparent),
              radial-gradient(1px 1px at 200px 150px, rgba(0, 255, 224, 0.6), transparent),
              radial-gradient(1px 1px at 300px 100px, rgba(255, 255, 255, 0.9), transparent),
              radial-gradient(2px 2px at 400px 200px, rgba(74, 144, 226, 0.7), transparent);
  background-repeat: repeat;
  background-size: 500px 300px;
  animation: constellationTwinkle 15s ease-in-out infinite;
}

.floating-stardust {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 20%, rgba(0, 255, 224, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 70% 80%, rgba(74, 144, 226, 0.08) 0%, transparent 50%);
  animation: stardustFloat 20s ease-in-out infinite;
}

.about-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--spacing-5);
  position: relative;
  z-index: 1;
}

/* Enhanced header styling */
.about-header {
  text-align: center;
  margin-bottom: var(--spacing-16);
}

.about-title {
  font-family: var(--font-primary);
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--spacing-6);
  line-height: 1.1;
}

.title-main {
  display: inline-block;
  color: var(--cosmic-white);
  margin-right: var(--spacing-3);
}

.title-accent {
  display: inline-block;
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.about-subtitle {
  font-size: clamp(1.1rem, 2.5vw, 1.5rem);
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto;
  line-height: var(--line-height-relaxed);
}

/* Company introduction section */
.about-intro {
  margin-bottom: var(--spacing-16);
}

.intro-content {
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
}

.intro-text {
  font-size: clamp(1.1rem, 2.5vw, 1.4rem);
  line-height: var(--line-height-relaxed);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--spacing-10);
}

.company-name {
  color: var(--accent-color);
  font-weight: var(--font-weight-bold);
  font-family: var(--font-primary);
}

.year-highlight {
  color: var(--highlight-color);
  font-weight: var(--font-weight-bold);
}

.text-gradient {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: var(--font-weight-semibold);
}

/* Introduction stats */
.intro-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--spacing-6);
  margin-top: var(--spacing-10);
}

.stat-item {
  background: rgba(20, 20, 35, 0.8);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-6);
  text-align: center;
  transition: all var(--duration-300) var(--ease-in-out);
}

.stat-item:hover {
  transform: translateY(-5px);
  border-color: var(--accent-color);
  box-shadow: 0 15px 30px rgba(0, 255, 224, 0.2);
}

.stat-icon {
  font-size: 2.5rem;
  margin-bottom: var(--spacing-3);
}

.stat-number {
  font-family: var(--font-primary);
  font-size: 2rem;
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  margin-bottom: var(--spacing-2);
  display: block;
}

.stat-label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Expertise section */
.expertise-section {
  background: rgba(15, 15, 25, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-10) var(--spacing-8);
  margin-bottom: var(--spacing-16);
  text-align: center;
}

.expertise-title {
  font-family: var(--font-primary);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-6);
}

.expertise-description {
  font-size: clamp(1rem, 2.5vw, 1.2rem);
  line-height: var(--line-height-relaxed);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--spacing-8);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.highlight-text {
  color: var(--accent-color);
  font-weight: var(--font-weight-semibold);
}

.market-stat {
  color: var(--highlight-color);
  font-weight: var(--font-weight-bold);
  font-size: 1.1em;
}

/* NASA link styling */
.nasa-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-4);
  background: rgba(0, 255, 224, 0.05);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-lg);
  padding: var(--spacing-4) var(--spacing-6);
  transition: all var(--duration-300) var(--ease-in-out);
}

.nasa-link:hover {
  background: rgba(0, 255, 224, 0.1);
  border-color: var(--accent-color);
}

.link-icon {
  font-size: 1.5rem;
}

.link-content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-1);
}

.link-text {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
}

.cosmic-link {
  color: var(--accent-color);
  text-decoration: none;
  font-weight: var(--font-weight-semibold);
  transition: all var(--duration-200) var(--ease-in-out);
}

.cosmic-link:hover {
  color: var(--highlight-color);
  transform: translateX(5px);
}

/* Values section */
.values-section {
  margin-top: var(--spacing-16);
}

.values-title {
  font-family: var(--font-primary);
  font-size: clamp(2rem, 4vw, 2.8rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  text-align: center;
  margin-bottom: var(--spacing-10);
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-8);
}

.value-card {
  position: relative;
  background: rgba(20, 20, 35, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-8);
  text-align: center;
  transition: all var(--duration-500) var(--ease-in-out);
  overflow: hidden;
}

.value-card:hover {
  transform: translateY(-10px);
  border-color: var(--accent-color);
  box-shadow: 0 20px 40px rgba(0, 255, 224, 0.2);
}

.card-cosmic-glow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 50% 50%, rgba(0, 255, 224, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity var(--duration-500) var(--ease-in-out);
  z-index: -1;
}

.value-card:hover .card-cosmic-glow {
  opacity: 1;
}

.value-icon {
  font-size: 3rem;
  margin-bottom: var(--spacing-4);
  display: block;
}

.value-title {
  font-family: var(--font-primary);
  font-size: 1.5rem;
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-4);
}

.value-description {
  font-size: 1rem;
  line-height: var(--line-height-relaxed);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--spacing-6);
}

.value-highlight {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--highlight-color) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: var(--font-weight-bold);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* General styles for sections */
.section-padding {
  padding: 60px 20px;
}

.section-dark {
  background-color: var(--primary-color);
  color: var(--cosmic-white);
}

.section-light {
  background-color: var(--neutral-color);
  color: var(--cosmic-white);
}

.section-title {
  font-family: 'Exo', sans-serif;
  color: var(--highlight-color);
  margin-bottom: 30px;
}

.section-subtitle {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  margin-bottom: 20px;
}

/* ==========================================================================
   ENHANCED SERVICES SECTION
   ========================================================================== */

.services-section {
  background: linear-gradient(135deg, var(--primary-color) 0%, #0D1B4D 30%, var(--secondary-color) 70%, var(--primary-color) 100%);
  padding: 120px 0;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}

/* Enhanced cosmic background system */
.services-cosmic-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.cosmic-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(0, 255, 224, 0.1) 1px, transparent 1px),
