  font-weight: var(--font-weight-semibold);
}

/* Introduction stats */
.intro-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--spacing-6);
  margin-top: var(--spacing-8);
}

.stat-item {
  text-align: center;
  padding: var(--spacing-6);
  background: rgba(0, 255, 224, 0.05);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-xl);
  transition: all var(--duration-300) var(--ease-in-out);
  position: relative;
  overflow: hidden;
}

.stat-item:hover {
  transform: translateY(-5px);
  border-color: var(--accent-color);
  box-shadow: 0 15px 30px rgba(0, 255, 224, 0.2);
}

.stat-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(0, 255, 224, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity var(--duration-300) var(--ease-in-out);
}

.stat-item:hover::before {
  opacity: 1;
}

.stat-icon {
  font-size: 2rem;
  margin-bottom: var(--spacing-3);
  animation: iconFloat 3s ease-in-out infinite;
}

.stat-number {
  font-family: var(--font-primary);
  font-size: 2rem;
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  margin-bottom: var(--spacing-2);
  line-height: 1;
}

.stat-label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: var(--font-weight-medium);
}

/* Expertise section */
.expertise-section {
  background: rgba(20, 20, 35, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(74, 144, 226, 0.3);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-10);
  margin-bottom: var(--spacing-16);
  position: relative;
  overflow: hidden;
}

.expertise-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 70% 30%, rgba(74, 144, 226, 0.08) 0%, transparent 50%);
  z-index: -1;
}

.expertise-title {
  font-family: var(--font-primary);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-6);
  text-align: center;
  line-height: 1.2;
}

.expertise-description {
  font-size: clamp(1rem, 2.2vw, 1.2rem);
  line-height: var(--line-height-relaxed);
  color: rgba(255, 255, 255, 0.9);
  text-align: center;
  margin-bottom: var(--spacing-8);
}

.market-stat {
  color: var(--highlight-color);
  font-weight: var(--font-weight-bold);
}

/* NASA link */
.nasa-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-4);
  background: rgba(0, 255, 224, 0.05);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-xl);
  padding: var(--spacing-5);
  transition: all var(--duration-300) var(--ease-in-out);
}

.nasa-link:hover {
  border-color: var(--accent-color);
  background: rgba(0, 255, 224, 0.1);
  transform: translateY(-2px);
}

.link-icon {
  font-size: 1.5rem;
  animation: iconFloat 3s ease-in-out infinite;
}

.link-content {
  text-align: left;
}

.link-text {
  display: block;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: var(--spacing-1);
}

.cosmic-link {
  color: var(--accent-color);
  text-decoration: none;
  font-weight: var(--font-weight-semibold);
  transition: color var(--duration-200) var(--ease-in-out);
}

.cosmic-link:hover {
  color: var(--highlight-color);
}

/* Values section */
.values-section {
  margin-top: var(--spacing-16);
}

.values-title {
  font-family: var(--font-primary);
  font-size: clamp(2rem, 4.5vw, 3rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  text-align: center;
  margin-bottom: var(--spacing-12);
  line-height: 1.2;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-8);
  margin-top: var(--spacing-10);
}

/* Enhanced value cards */
.value-card {
  position: relative;
  background: rgba(15, 15, 25, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-10);
  text-align: center;
  transition: all var(--duration-500) var(--ease-in-out);
  overflow: hidden;
  transform-style: preserve-3d;
}

.value-card:hover {
  transform: translateY(-10px) rotateX(5deg);
  border-color: var(--accent-color);
  box-shadow: 0 25px 50px rgba(0, 255, 224, 0.3);
}

.card-cosmic-glow {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, var(--accent-color), var(--highlight-color), var(--accent-color));
  border-radius: var(--radius-2xl);
  opacity: 0;
  transition: opacity var(--duration-500) var(--ease-in-out);
  z-index: -2;
  filter: blur(15px);
}

.value-card:hover .card-cosmic-glow {
  opacity: 0.4;
}

.value-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 50% 50%, rgba(0, 255, 224, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity var(--duration-500) var(--ease-in-out);
  z-index: -1;
}

.value-card:hover::before {
  opacity: 1;
}

.value-icon {
  font-size: 4rem;
  margin-bottom: var(--spacing-6);
  animation: iconFloat 4s ease-in-out infinite;
  filter: drop-shadow(0 0 20px rgba(0, 255, 224, 0.3));
}

.value-title {
  font-family: var(--font-primary);
  font-size: 1.8rem;
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-4);
  line-height: 1.2;
}

.value-description {
  font-size: 1.1rem;
  line-height: var(--line-height-relaxed);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--spacing-6);
}

.value-highlight {
  display: inline-block;
  background: linear-gradient(135deg, var(--accent-color), var(--highlight-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: var(--font-weight-bold);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: var(--spacing-2) var(--spacing-4);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-lg);
  backdrop-filter: blur(10px);
}

/* Responsive design for About section */
@media (max-width: 768px) {
  .about-section {
    padding: var(--spacing-16) 0;
  }
  
  .about-container {
    padding: 0 var(--spacing-4);
  }
  
  .intro-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--spacing-4);
  }
  
  .values-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-6);
  }
  
  .value-card {
    padding: var(--spacing-8);
  }
  
  .nasa-link {
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .intro-stats {
    grid-template-columns: 1fr;
  }
  
  .stat-item {
    padding: var(--spacing-4);
  }
  
  .value-card {
    padding: var(--spacing-6);
  }
  
  .value-icon {
    font-size: 3rem;
  }
}

/* ==========================================================================
   ENHANCED MARKET INSIGHTS SECTION
   ========================================================================== */

.insights-section {
  position: relative;
  background: linear-gradient(135deg, var(--secondary-color) 0%, #0a0a1a 50%, var(--primary-color) 100%);
  color: var(--cosmic-white);
  padding: var(--spacing-20) 0;
  overflow: hidden;
  min-height: 100vh;
}

/* Cosmic background elements */
.insights-cosmic-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
}

.cosmic-data-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(0, 255, 224, 0.1) 1px, transparent 1px),
              linear-gradient(rgba(0, 255, 224, 0.1) 1px, transparent 1px);
  background-size: 100px 100px;
  animation: dataGridPulse 15s ease-in-out infinite;
  opacity: 0.3;
}

.floating-data-points {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(3px 3px at 50px 100px, rgba(74, 144, 226, 0.8), transparent),
              radial-gradient(2px 2px at 150px 200px, rgba(0, 255, 224, 0.6), transparent),
              radial-gradient(4px 4px at 250px 50px, rgba(255, 255, 255, 0.4), transparent),
              radial-gradient(2px 2px at 350px 150px, rgba(74, 144, 226, 0.5), transparent);
  background-repeat: repeat;
  background-size: 400px 300px;
  animation: dataPointsFloat 25s linear infinite;
}

/* Insights container */
.insights-container {
  position: relative;
  z-index: 2;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--spacing-5);
}

/* Enhanced section header */
.insights-header {
  text-align: center;
  margin-bottom: var(--spacing-16);
}

.insights-title {
  font-family: var(--font-primary);
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--spacing-6);
  line-height: 1.1;
}

.insights-subtitle {
  font-size: clamp(1.1rem, 2.5vw, 1.4rem);
  color: rgba(255, 255, 255, 0.8);
  max-width: 700px;
  margin: 0 auto;
  line-height: var(--line-height-relaxed);
}

/* Market overview stats */
.market-overview {
  margin-bottom: var(--spacing-16);
}

.overview-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-6);
  max-width: 900px;
  margin: 0 auto;
}

.overview-stat {
  background: rgba(15, 15, 25, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-8);
  text-align: center;
  transition: all var(--duration-300) var(--ease-in-out);
  position: relative;
  overflow: hidden;
}

.overview-stat:hover {
  transform: translateY(-8px);
  border-color: var(--accent-color);
  box-shadow: 0 20px 40px rgba(0, 255, 224, 0.3);
}

.overview-stat::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at center, rgba(0, 255, 224, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity var(--duration-300) var(--ease-in-out);
}

.overview-stat:hover::before {
  opacity: 1;
}

.overview-stat .stat-icon {
  font-size: 3rem;
  margin-bottom: var(--spacing-4);
  animation: iconFloat 4s ease-in-out infinite;
}

.overview-stat .stat-value {
  font-family: var(--font-primary);
  font-size: 2.5rem;
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  margin-bottom: var(--spacing-2);
  line-height: 1;
}

.overview-stat .stat-label {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Market category sections */
.market-category {
  margin-bottom: var(--spacing-16);
}

.category-header {
  text-align: center;
  margin-bottom: var(--spacing-10);
}

.category-title {
  font-family: var(--font-primary);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-4);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-3);
  line-height: 1.2;
}

.category-icon {
  font-size: 2rem;
  animation: iconFloat 3s ease-in-out infinite;
}

.category-description {
  font-size: clamp(1rem, 2.2vw, 1.2rem);
  color: rgba(255, 255, 255, 0.8);
  max-width: 800px;
  margin: 0 auto;
  line-height: var(--line-height-relaxed);
}

/* Insights grid */
.insights-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--spacing-6);
  margin-top: var(--spacing-8);
}

/* Enhanced insight cards */
.insight-card {
  position: relative;
  background: rgba(15, 15, 25, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-8);
  transition: all var(--duration-500) var(--ease-in-out);
  overflow: hidden;
  transform-style: preserve-3d;
}

.insight-card:hover {
  transform: translateY(-10px) rotateX(2deg);
  border-color: var(--accent-color);
  box-shadow: 0 25px 50px rgba(0, 255, 224, 0.3);
}

.insight-card.space-themed {
  border-color: rgba(74, 144, 226, 0.4);
}

.insight-card.space-themed:hover {
  border-color: var(--highlight-color);
  box-shadow: 0 25px 50px rgba(74, 144, 226, 0.3);
}

.card-cosmic-glow {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, var(--accent-color), var(--highlight-color), var(--accent-color));
  border-radius: var(--radius-2xl);
  opacity: 0;
  transition: opacity var(--duration-500) var(--ease-in-out);
  z-index: -2;
  filter: blur(15px);
}

.insight-card:hover .card-cosmic-glow {
  opacity: 0.4;
}

.insight-card.space-themed .card-cosmic-glow {
  background: linear-gradient(45deg, var(--highlight-color), var(--accent-color), var(--highlight-color));
}

.insight-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 30% 70%, rgba(0, 255, 224, 0.08) 0%, transparent 50%);
  opacity: 0;
  transition: opacity var(--duration-500) var(--ease-in-out);
  z-index: -1;
}

.insight-card:hover::before {
  opacity: 1;
}

.insight-card.space-themed::before {
  background: radial-gradient(circle at 30% 70%, rgba(74, 144, 226, 0.08) 0%, transparent 50%);
}

/* Card header */
.card-header {
  margin-bottom: var(--spacing-6);
  position: relative;
}

.card-title {
  font-family: var(--font-primary);
  font-size: 1.4rem;
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-2);
  line-height: 1.3;
}

.card-category {
  display: inline-block;
  background: rgba(0, 255, 224, 0.1);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-full);
  padding: var(--spacing-1) var(--spacing-3);
  font-size: 0.8rem;
  font-weight: var(--font-weight-medium);
  color: var(--accent-color);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.space-themed .card-category {
  background: rgba(74, 144, 226, 0.1);
  border-color: rgba(74, 144, 226, 0.3);
  color: var(--highlight-color);
}

/* Card content */
.card-content {
  position: relative;
}

.projection-value {
  font-family: var(--font-primary);
  font-size: 1.6rem;
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  margin-bottom: var(--spacing-4);
  line-height: 1.2;
}

.space-themed .projection-value {
  color: var(--highlight-color);
}

.growth-rate {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  margin-bottom: var(--spacing-4);
  padding: var(--spacing-3);
  background: rgba(0, 255, 224, 0.05);
  border: 1px solid rgba(0, 255, 224, 0.2);
  border-radius: var(--radius-lg);
}

.space-themed .growth-rate {
  background: rgba(74, 144, 226, 0.05);
  border-color: rgba(74, 144, 226, 0.2);
}

.growth-icon {
  font-size: 1.2rem;
}

.growth-text {
  font-size: 1rem;
  font-weight: var(--font-weight-semibold);
  color: rgba(255, 255, 255, 0.9);
}

.source-info {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  font-size: 0.9rem;
}

.source-label {
  color: rgba(255, 255, 255, 0.6);
  font-weight: var(--font-weight-medium);
}

.source-name {
  color: rgba(255, 255, 255, 0.8);
  font-weight: var(--font-weight-semibold);
}

/* Enhanced CTA section */
.insights-cta {
  position: relative;
  background: rgba(15, 15, 25, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(0, 255, 224, 0.3);
  border-radius: var(--radius-2xl);
  padding: var(--spacing-12);
  text-align: center;
  overflow: hidden;
  margin-top: var(--spacing-16);
}

.cta-cosmic-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 50% 50%, rgba(0, 255, 224, 0.1) 0%, transparent 70%);
  animation: ctaPulse 8s ease-in-out infinite;
  z-index: -1;
}

.cta-content {
  position: relative;
  z-index: 2;
}

.insights-cta .cta-icon {
  font-size: 3.5rem;
  margin-bottom: var(--spacing-6);
  animation: iconFloat 4s ease-in-out infinite;
}

.insights-cta .cta-title {
  font-family: var(--font-primary);
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: var(--font-weight-bold);
  color: var(--cosmic-white);
  margin-bottom: var(--spacing-6);
  line-height: 1.2;
}

.insights-cta .cta-description {
  font-size: clamp(1rem, 2.5vw, 1.3rem);
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: var(--spacing-10);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: var(--line-height-relaxed);
}

.insights-cta .cta-buttons {
  display: flex;
  gap: var(--spacing-5);
  justify-content: center;
  flex-wrap: wrap;
}

/* Animation keyframes for insights */
@keyframes dataGridPulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.5; transform: scale(1.02); }
}

@keyframes dataPointsFloat {
  0% { transform: translateX(0px) translateY(0px); }
  25% { transform: translateX(15px) translateY(-10px); }
  50% { transform: translateX(-10px) translateY(15px); }
  75% { transform: translateX(-15px) translateY(-10px); }
  100% { transform: translateX(0px) translateY(0px); }
}

/* Responsive design for insights section */
@media (max-width: 768px) {
  .insights-section {
    padding: var(--spacing-16) 0;
  }
  
  .insights-container {
    padding: 0 var(--spacing-4);
  }
  
  .overview-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: var(--spacing-4);
  }
  
  .insights-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-5);
  }
  
  .insight-card {
    padding: var(--spacing-6);
  }
  
  .category-title {
    flex-direction: column;
    gap: var(--spacing-2);
  }
  
  .insights-cta {
    padding: var(--spacing-8);
  }
  
  .insights-cta .cta-buttons {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .overview-stats {
    grid-template-columns: 1fr;
  }
  
  .overview-stat {
    padding: var(--spacing-6);
  }
  
  .insight-card {
    padding: var(--spacing-5);
  }
  
  .card-title {
    font-size: 1.2rem;
  }
  
  .projection-value {
    font-size: 1.4rem;
  }
}

/* Legacy Market Insights Section (keeping for backward compatibility) */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  color: var(--cosmic-white);
}

table th,
table td {
  border: 1px solid var(--space-gray);
  padding: 10px;
  text-align: left;
}

table th {
  background-color: var(--neutral-color);
  color: var(--accent-color);
}

/* Contact Section */
.contact-form {
  max-width: 600px;
  margin: auto;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid var(--space-gray);
  background-color: var(--neutral-color);
  color: var(--cosmic-white);
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.contact-form button {
  background-color: var(--highlight-color);
  color: var(--cosmic-white);
  padding: 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.contact-form button:hover:not(:disabled) {
  background-color: var(--accent-color);
}

.contact-form button:disabled {
  background-color: var(--space-gray);
  cursor: not-allowed;
  opacity: 0.7;
}

.submit-status {
  margin-top: 15px;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
}

.submit-status.success {
  background-color: rgba(0, 255, 0, 0.1);
  color: #00ff00;
  border: 1px solid rgba(0, 255, 0, 0.3);
}

.submit-status.error {
  background-color: rgba(255, 0, 0, 0.1);
  color: #ff6b6b;
  border: 1px solid rgba(255, 0, 0, 0.3);
}

.contact-info {
  text-align: center;
  margin-top: 40px;
}

.contact-info a {
  color: var(--accent-color);
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

/* Footer */
.footer {
  background-color: var(--primary-color);
  color: var(--cosmic-white);
  text-align: center;
  padding: 20px;
  font-size: 14px;
}

.footer a {
  color: var(--accent-color);
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}

/* Font Imports */
@import url('https://fonts.googleapis.com/css2?family=Exo:wght@400;700&family=Roboto:wght@400;700&display=swap');

/* Color Variables */
:root {
  --primary-color: #0A0F3D; /* Midnight Blue */
  --accent-color: #00FFE0;  /* Neon Cyan */
  --highlight-color: #FF00D1; /* Star Magenta */
  --neutral-color: #1E1E2F;   /* Space Gray */
  --cosmic-white: #FFFFFF; /* Cosmic White */
  --space-gray: #1E1E2F; /* Re-using neutral for consistency */
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: var(--primary-color);
  color: var(--cosmic-white);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Exo', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .hero-title {
    font-size: 3rem;
  }

  .hero-tagline {
    font-size: 1.5rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  /* Modern Services Mobile Styles */
  .services-title {
    font-size: 2.5rem;
  }

  .services-subtitle {
    font-size: 1.1rem;
    padding: 0 10px;
  }

  .services-stats {
    flex-direction: column;
    gap: 30px;
  }

  .services-grid-modern {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 0 10px;
  }

  .service-card-modern {
    padding: 30px 20px;
  }

  .services-cta-section {
    padding: 40px 20px;
    margin: 0 10px;
  }

  .cta-title {
    font-size: 2rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .cta-primary,
  .cta-secondary {
    width: 100%;
    max-width: 300px;
  }

  /* Legacy Services Mobile Styles */
  .services-grid {
    flex-direction: column;
    align-items: center;
  }

  .service-card {
    width: 90%;
  }

  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap; /* Prevent text wrapping in cells */
  }

  table thead,
  table tbody,
  table th,
  table td,
  table tr {
    display: block;
  }

  table th {
    text-align: right;
  }

  table td {
    text-align: right;
    padding-left: 50%;
    position: relative;
  }

  table td::before {
    content: attr(data-label);
    position: absolute;
    left: 0;
    width: 45%;
    padding-left: 15px;
    font-weight: bold;
    text-align: left;
  }
}

/* Tablet Responsive Styles */
@media (max-width: 1024px) and (min-width: 769px) {
  .services-grid-modern {
    grid-template-columns: repeat(2, 1fr);
  }

  .services-stats {
    gap: 40px;
  }

  .services-title {
    font-size: 3rem;
  }

  .cta-title {
    font-size: 2.2rem;
  }
}

/* Large Mobile Responsive Styles */
@media (max-width: 480px) {
  .services-section {
    padding: 60px 0;
  }

  .services-container {
    padding: 0 15px;
  }

  .services-title {
    font-size: 2rem;
  }

  .services-subtitle {
    font-size: 1rem;
  }

  .stat-number {
    font-size: 2rem;
  }

  .service-card-modern {
    padding: 25px 15px;
  }

  .service-icon {
    font-size: 2.5rem;
  }

  .service-title {
    font-size: 1.3rem;
  }

  .services-cta-section {
    padding: 30px 15px;
  }

  .cta-title {
    font-size: 1.8rem;
  }

  .cta-description {
    font-size: 1rem;
  }
}

/* Header Styles */
.main-header {
  background-color: var(--primary-color);
  padding: 15px 20px;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.navbar-brand {
  display: flex;
  align-items: center;
  color: var(--cosmic-white);
  text-decoration: none;
  font-family: 'Exo', sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
}

.navbar-logo {
  height: 40px;
  margin-right: 10px;
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-links li {
  margin-left: 30px;
}

.nav-links a {
  color: var(--cosmic-white);
  text-decoration: none;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: var(--accent-color);
}

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}

/* Back to Top Button */
.back-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background-color: var(--highlight-color);
  color: var(--cosmic-white);
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 999;
}

.back-to-top.show {
  opacity: 1;
  visibility: visible;
}

/* Privacy Policy and Terms Pages */
.privacy-policy-page,
.terms-of-service-page {
  background-color: var(--deep-space);
  color: var(--cosmic-white);
  min-height: 100vh;
  padding: 80px 0 40px;
}

.privacy-policy-page .container,
.terms-of-service-page .container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
  line-height: 1.6;
}

.privacy-policy-page h1,
.terms-of-service-page h1 {
  color: var(--accent-color);
  text-align: center;
  margin-bottom: 10px;
  font-size: 2.5rem;
}

.privacy-policy-page .last-updated,
.terms-of-service-page .last-updated {
  text-align: center;
  color: var(--space-gray);
  margin-bottom: 40px;
  font-style: italic;
}

.privacy-policy-page section,
.terms-of-service-page section {
  margin-bottom: 30px;
}

.privacy-policy-page h2,
.terms-of-service-page h2 {
  color: var(--highlight-color);
  margin-bottom: 15px;
  font-size: 1.5rem;
  border-bottom: 2px solid var(--space-gray);
  padding-bottom: 5px;
}

.privacy-policy-page p,
.terms-of-service-page p {
  margin-bottom: 15px;
}

.privacy-policy-page ul,
.terms-of-service-page ul {
  margin-bottom: 15px;
  padding-left: 20px;
}

.privacy-policy-page li,
.terms-of-service-page li {
  margin-bottom: 8px;
}

.privacy-policy-page a,
.terms-of-service-page a {
  color: var(--accent-color);
  text-decoration: none;
}

.privacy-policy-page a:hover,
.terms-of-service-page a:hover {
  text-decoration: underline;
}

/* Page overlay for modal-like behavior */
.page-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  overflow-y: auto;
}

.page-overlay .close-button {
  position: fixed;
  top: 20px;
  right: 20px;
  background: var(--highlight-color);
  color: var(--cosmic-white);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 20px;
  cursor: pointer;
  z-index: 1001;
  transition: background-color 0.3s ease;
}

.page-overlay .close-button:hover {
  background-color: var(--accent-color);
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .nav-links {
    margin-top: 15px;
    flex-direction: column;
    width: 100%;
  }

  .nav-links li {
    margin: 10px 0;
    width: 100%;
    text-align: center;
  }
  
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .hero p {
    font-size: 1.1rem;
  }
  
  .section-padding {
    padding: 40px 20px;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .services-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .contact-form {
    padding: 0 10px;
  }
  
  .contact-form input,
  .contact-form textarea {
    font-size: 16px; /* Prevents zoom on iOS */
  }
  
  .back-to-top {
    bottom: 20px;
    right: 20px;
    width: 45px;
    height: 45px;
    font-size: 1.2rem;
  }
  
  .privacy-policy-page h1,
  .terms-of-service-page h1 {
    font-size: 2rem;
  }
  
  .privacy-policy-page .container,
  .terms-of-service-page .container {
    padding: 0 15px;
  }
  
  .page-overlay .close-button {
    top: 15px;
    right: 15px;
    width: 35px;
    height: 35px;
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .hero h1 {
    font-size: 2rem;
  }
  
  .hero p {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 1.8rem;
  }
  
  .footer p {
    font-size: 0.9rem;
    line-height: 1.6;
  }
  
  .footer-link {
    margin: 0 5px;
  }
}