/**
 * CSS Custom Properties (Variables)
 * 
 * This file defines all CSS custom properties used throughout the Astrobyte.com application.
 * These variables ensure consistent theming, spacing, and typography across all components.
 * 
 * Categories:
 * - Color Palette: Primary, secondary, accent, and neutral colors
 * - Typography: Font families, sizes, and weights
 * - Spacing: Consistent spacing scale for margins and padding
 * - Layout: Breakpoints and container widths
 * - Animation: Timing functions and durations
 * 
 * Usage:
 * Use these variables in component styles with var(--variable-name)
 * Example: color: var(--primary-color);
 * 
 * @author Astrobyte Development Team
 * @version 1.0.0
 */

:root {
  /* ==========================================================================
     COLOR PALETTE
     ========================================================================== */
  
  /* Primary Colors - Enhanced 2025 cosmic theme with better contrast */
  --primary-color: #0a0a1a;         /* Deep space black - main background */
  --primary-light: #1a1a2e;         /* Lighter space navy - elevated surfaces */
  --secondary-color: #16213e;       /* Darker navy - secondary backgrounds */
  --accent-color: #00ffe0;          /* Cyan glow - primary CTAs and highlights */
  --accent-secondary: #4a90e2;      /* Electric blue - secondary accents */
  
  /* Neutral Colors - Enhanced contrast and accessibility */
  --cosmic-white: #ffffff;          /* Pure white - primary text on dark */
  --cosmic-gray-50: #fafafa;        /* Lightest gray - subtle backgrounds */
  --cosmic-gray-100: #f5f5f5;       /* Light gray - card backgrounds */
  --cosmic-gray-200: #e5e5e5;       /* Medium light gray - borders */
  --cosmic-gray-300: #d4d4d4;       /* Medium gray - disabled states */
  --cosmic-gray-400: #a3a3a3;       /* Dark gray - secondary text */
  --cosmic-gray-500: #737373;       /* Darker gray - muted text */
  --cosmic-gray-600: #525252;       /* Very dark gray - icons */
  --cosmic-gray-700: #404040;       /* Almost black - headings on light */
  --cosmic-gray-800: #262626;       /* Dark surface - elevated dark */
  --cosmic-gray-900: #171717;       /* Darkest - text on light */
  --neutral-color: #2c2c54;         /* Medium purple-gray - content sections */
  --highlight-color: #4a90e2;       /* Bright blue - section titles and links */
  
  /* Gradient Colors - Modern 2025 gradients */
  --gradient-primary: linear-gradient(135deg, var(--primary-color) 0%, var(--secondary-color) 50%, var(--accent-color) 100%);
  --gradient-accent: linear-gradient(135deg, var(--accent-color) 0%, var(--accent-secondary) 100%);
  --gradient-cosmic: linear-gradient(135deg, #00ffe0 0%, #4a90e2 50%, #8b5cf6 100%);
  --gradient-text: linear-gradient(135deg, var(--accent-color) 0%, var(--accent-secondary) 100%);
  
  /* Status Colors - For feedback and states */
  --success-color: #28a745;         /* Green - success messages */
  --warning-color: #ffc107;         /* Yellow - warning messages */
  --error-color: #dc3545;           /* Red - error messages */
  --info-color: #17a2b8;            /* Cyan - informational messages */
  
  /* ==========================================================================
     TYPOGRAPHY
     ========================================================================== */
  
  /* Font Families - Enhanced 2025 typography stack */
  --font-primary: 'Inter', 'Exo', system-ui, -apple-system, sans-serif;     /* Headers and titles - modern, clean */
  --font-secondary: 'Inter', 'Roboto', system-ui, -apple-system, sans-serif; /* Body text - high readability */
  --font-display: 'Space Grotesk', 'Exo', system-ui, sans-serif;            /* Display text - futuristic feel */
  --font-monospace: 'JetBrains Mono', 'Fira Code', 'Courier New', monospace; /* Code and technical content */
  
  /* Font Sizes - Responsive scale */
  --font-size-xs: 0.75rem;          /* 12px - small labels */
  --font-size-sm: 0.875rem;         /* 14px - small text */
  --font-size-base: 1rem;           /* 16px - base body text */
  --font-size-lg: 1.125rem;         /* 18px - large body text */
  --font-size-xl: 1.25rem;          /* 20px - small headings */
  --font-size-2xl: 1.5rem;          /* 24px - medium headings */
  --font-size-3xl: 1.875rem;        /* 30px - large headings */
  --font-size-4xl: 2.25rem;         /* 36px - extra large headings */
  --font-size-5xl: 3rem;            /* 48px - hero titles */
  
  /* Font Weights */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extrabold: 800;
  
  /* Line Heights */
  --line-height-tight: 1.25;
  --line-height-normal: 1.5;
  --line-height-relaxed: 1.75;
  
  /* ==========================================================================
     SPACING SYSTEM
     ========================================================================== */
  
  /* Base spacing unit: 0.25rem (4px) */
  --spacing-1: 0.25rem;             /* 4px */
  --spacing-2: 0.5rem;              /* 8px */
  --spacing-3: 0.75rem;             /* 12px */
  --spacing-4: 1rem;                /* 16px */
  --spacing-5: 1.25rem;             /* 20px */
  --spacing-6: 1.5rem;              /* 24px */
  --spacing-8: 2rem;                /* 32px */
  --spacing-10: 2.5rem;             /* 40px */
  --spacing-12: 3rem;               /* 48px */
  --spacing-16: 4rem;               /* 64px */
  --spacing-20: 5rem;               /* 80px */
  --spacing-24: 6rem;               /* 96px */
  --spacing-32: 8rem;               /* 128px */
  
  /* Semantic spacing aliases */
  --spacing-xs: var(--spacing-2);   /* Extra small spacing */
  --spacing-sm: var(--spacing-4);   /* Small spacing */
  --spacing-md: var(--spacing-6);   /* Medium spacing */
  --spacing-lg: var(--spacing-8);   /* Large spacing */
  --spacing-xl: var(--spacing-12);  /* Extra large spacing */
  --spacing-2xl: var(--spacing-16); /* 2x extra large spacing */
  --spacing-3xl: var(--spacing-24); /* 3x extra large spacing */
  
  /* ==========================================================================
     LAYOUT AND BREAKPOINTS
     ========================================================================== */
  
  /* Breakpoints for responsive design */
  --breakpoint-xs: 480px;           /* Extra small devices */
  --breakpoint-sm: 640px;           /* Small devices */
  --breakpoint-md: 768px;           /* Medium devices (tablets) */
  --breakpoint-lg: 1024px;          /* Large devices (desktops) */
  --breakpoint-xl: 1280px;          /* Extra large devices */
  --breakpoint-2xl: 1536px;         /* 2x extra large devices */
  
  /* Container widths */
  --container-sm: 640px;
  --container-md: 768px;
  --container-lg: 1024px;
  --container-xl: 1280px;
  --container-2xl: 1536px;
  
  /* Layout spacing */
  --section-padding-y: var(--spacing-16); /* Vertical section padding */
  --section-padding-x: var(--spacing-5);  /* Horizontal section padding */
  --container-padding: var(--spacing-4);   /* Container horizontal padding */
  
  /* ==========================================================================
     BORDERS AND RADIUS
     ========================================================================== */
  
  /* Border radius scale */
  --radius-none: 0;
  --radius-sm: 0.125rem;            /* 2px */
  --radius-base: 0.25rem;           /* 4px */
  --radius-md: 0.375rem;            /* 6px */
  --radius-lg: 0.5rem;              /* 8px */
  --radius-xl: 0.75rem;             /* 12px */
  --radius-2xl: 1rem;               /* 16px */
  --radius-full: 9999px;            /* Fully rounded */
  
  /* Border widths */
  --border-width-0: 0;
  --border-width-1: 1px;
  --border-width-2: 2px;
  --border-width-4: 4px;
  --border-width-8: 8px;
  
  /* ==========================================================================
     SHADOWS AND EFFECTS
     ========================================================================== */
  
  /* Box shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-base: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  /* ==========================================================================
     ANIMATION AND TRANSITIONS - Enhanced 2025 motion design
     ========================================================================== */
  
  /* Transition durations */
  --duration-75: 75ms;
  --duration-100: 100ms;
  --duration-150: 150ms;
  --duration-200: 200ms;
  --duration-300: 300ms;
  --duration-500: 500ms;
  --duration-700: 700ms;
  --duration-1000: 1000ms;
  
  /* Enhanced timing functions for modern feel */
  --ease-linear: linear;
  --ease-in: cubic-bezier(0.4, 0, 1, 1);
  --ease-out: cubic-bezier(0, 0, 0.2, 1);
  --ease-in-out: cubic-bezier(0.4, 0, 0.2, 1);
  --ease-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --ease-elastic: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --ease-smooth: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  
  /* Common transitions */
  --transition-colors: color var(--duration-200) var(--ease-smooth),
                       background-color var(--duration-200) var(--ease-smooth),
                       border-color var(--duration-200) var(--ease-smooth);
  --transition-opacity: opacity var(--duration-200) var(--ease-smooth);
  --transition-transform: transform var(--duration-300) var(--ease-smooth);
  --transition-all: all var(--duration-200) var(--ease-smooth);
  --transition-bounce: transform var(--duration-300) var(--ease-bounce);
  --transition-elastic: transform var(--duration-500) var(--ease-elastic);
  
  /* ==========================================================================
     Z-INDEX SCALE
     ========================================================================== */
  
  /* Z-index values for consistent layering */
  --z-index-hide: -1;
  --z-index-auto: auto;
  --z-index-base: 0;
  --z-index-docked: 10;
  --z-index-dropdown: 1000;
  --z-index-sticky: 1020;
  --z-index-banner: 1030;
  --z-index-overlay: 1040;
  --z-index-modal: 1050;
  --z-index-popover: 1060;
  --z-index-skipLink: 1070;
  --z-index-toast: 1080;
  --z-index-tooltip: 1090;
  
  /* ==========================================================================
     ACCESSIBILITY AND MOTION PREFERENCES
     ========================================================================== */
  
  /* Reduced motion support */
  --motion-reduce: no-preference;
  --motion-safe: no-preference;
  
  /* Focus styles */
  --focus-ring: 0 0 0 2px var(--accent-color);
  --focus-ring-offset: 0 0 0 4px rgba(0, 255, 224, 0.2);
  
  /* High contrast mode support */
  --border-contrast: var(--cosmic-gray-300);
  --text-contrast: var(--cosmic-gray-900);
  
  /* Dark mode toggle (for future implementation) */
  --dark-mode-bg: #0a0a0a;
  --dark-mode-text: #ffffff;
  --dark-mode-accent: #00ffe0;
  
  /* Theme toggle variables */
  --theme-toggle-size: 48px;
  --theme-toggle-bg: rgba(255, 255, 255, 0.1);
  --theme-toggle-border: rgba(255, 255, 255, 0.2);
  --theme-toggle-hover: rgba(0, 255, 224, 0.2);
}