/**
 * CSS Reset and Base Styles
 * 
 * This file provides a modern CSS reset and base styles for the Astrobyte.com application.
 * It normalizes browser defaults and establishes consistent base styling across all browsers.
 * 
 * Features:
 * - Modern CSS reset based on Josh Comeau's custom reset
 * - Box-sizing normalization
 * - Typography base styles
 * - Form element normalization
 * - Accessibility improvements
 * - Performance optimizations
 * 
 * @author Astrobyte Development Team
 * @version 1.0.0
 */

/* ==========================================================================
   MODERN CSS RESET
   ========================================================================== */

/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*
  2. Remove default margin and padding
*/
* {
  margin: 0;
  padding: 0;
}

/*
  3. Allow percentage-based heights in the application
*/
html,
body {
  height: 100%;
}

/*
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: var(--line-height-normal);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-feature-settings: "kern" 1;
  font-variant-ligatures: common-ligatures;
}

/*
  6. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

/*
  7. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
}

/*
  8. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
  hyphens: auto;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* ==========================================================================
   BASE TYPOGRAPHY
   ========================================================================== */

html {
  font-size: 16px; /* Base font size for rem calculations */
  scroll-behavior: smooth; /* Smooth scrolling for anchor links */
}

body {
  font-family: var(--font-secondary);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-normal);
  color: var(--cosmic-white);
  background-color: var(--primary-color);
}

/* Heading styles */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-primary);
  font-weight: var(--font-weight-bold);
  line-height: var(--line-height-tight);
  margin-bottom: var(--spacing-4);
}

h1 {
  font-size: var(--font-size-5xl);
}

h2 {
  font-size: var(--font-size-4xl);
}

h3 {
  font-size: var(--font-size-3xl);
}

h4 {
  font-size: var(--font-size-2xl);
}

h5 {
  font-size: var(--font-size-xl);
}

h6 {
  font-size: var(--font-size-lg);
}

/* Paragraph and text styles */
p {
  margin-bottom: var(--spacing-4);
  line-height: var(--line-height-relaxed);
}

/* Link styles */
a {
  color: var(--highlight-color);
  text-decoration: none;
  transition: var(--transition-colors);
}

a:hover,
a:focus {
  color: var(--accent-color);
  text-decoration: underline;
}

a:focus {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

/* List styles */
ul,
ol {
  margin-bottom: var(--spacing-4);
  padding-left: var(--spacing-6);
}

li {
  margin-bottom: var(--spacing-2);
}

/* Remove list styles for navigation lists */
nav ul,
nav ol {
  list-style: none;
  padding-left: 0;
}

nav li {
  margin-bottom: 0;
}

/* ==========================================================================
   FORM ELEMENTS
   ========================================================================== */

/* Form container */
form {
  margin-bottom: var(--spacing-6);
}

/* Input fields */
input,
textarea,
select {
  width: 100%;
  padding: var(--spacing-3) var(--spacing-4);
  border: var(--border-width-1) solid var(--neutral-color);
  border-radius: var(--radius-md);
  background-color: var(--cosmic-white);
  color: var(--primary-color);
  font-size: var(--font-size-base);
  line-height: var(--line-height-normal);
  transition: var(--transition-colors);
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px rgba(15, 52, 96, 0.1);
}

input::placeholder,
textarea::placeholder {
  color: #6b7280;
  opacity: 1;
}

/* Textarea specific styles */
textarea {
  resize: vertical;
  min-height: 120px;
}

/* Button styles */
button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-3) var(--spacing-6);
  border: none;
  border-radius: var(--radius-md);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition-all);
  user-select: none;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Label styles */
label {
  display: block;
  margin-bottom: var(--spacing-2);
  font-weight: var(--font-weight-medium);
  color: var(--cosmic-white);
}

/* ==========================================================================
   ACCESSIBILITY IMPROVEMENTS
   ========================================================================== */

/* Focus styles for keyboard navigation */
*:focus {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

/* Remove focus outline for mouse users */
*:focus:not(:focus-visible) {
  outline: none;
}

/* Ensure focus is visible for keyboard users */
*:focus-visible {
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

/* Screen reader only content */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Skip to main content link */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--primary-color);
  color: var(--cosmic-white);
  padding: 8px;
  text-decoration: none;
  border-radius: var(--radius-md);
  z-index: var(--z-index-skipLink);
}

.skip-link:focus {
  top: 6px;
}

/* ==========================================================================
   REDUCED MOTION PREFERENCES
   ========================================================================== */

/* Respect user's motion preferences */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* ==========================================================================
   HIGH CONTRAST MODE SUPPORT
   ========================================================================== */

/* Support for Windows High Contrast mode */
@media (prefers-contrast: high) {
  * {
    border-color: ButtonText;
  }
  
  button {
    border: 1px solid ButtonText;
  }
}

/* ==========================================================================
   PRINT STYLES
   ========================================================================== */

@media print {
  *,
  *::before,
  *::after {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  
  a,
  a:visited {
    text-decoration: underline;
  }
  
  a[href]::after {
    content: " (" attr(href) ")";
  }
  
  abbr[title]::after {
    content: " (" attr(title) ")";
  }
  
  img {
    page-break-inside: avoid;
  }
  
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  
  h2,
  h3 {
    page-break-after: avoid;
  }
}