/**
 * Enhanced Form Components - 2025 Edition
 * 
 * Modern form styling with improved UX, accessibility, and validation states.
 * 
 * Features:
 * - Enhanced visual design with glassmorphism effects
 * - Real-time validation with smooth animations
 * - Improved accessibility with proper focus states
 * - Loading states and micro-interactions
 * - Mobile-optimized touch targets
 * - Progressive disclosure patterns
 * 
 * @author Astrobyte Development Team
 * @version 2.0.0
 */

/* ==========================================================================
   FORM CONTAINER
   ========================================================================== */

.form-container {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-xl);
  padding: var(--spacing-8);
  position: relative;
  overflow: hidden;
}

.form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--gradient-cosmic);
  opacity: 0.7;
}

/* ==========================================================================
   FORM GROUPS
   ========================================================================== */

.form-group {
  position: relative;
  margin-bottom: var(--spacing-6);
}

.form-group.focused {
  z-index: 1;
}

/* ==========================================================================
   LABELS
   ========================================================================== */

.form-label {
  display: block;
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--cosmic-gray-300);
  margin-bottom: var(--spacing-2);
  transition: var(--transition-colors);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.form-group.focused .form-label,
.form-group.has-value .form-label {
  color: var(--accent-color);
}

/* Floating labels */
.form-group.floating {
  position: relative;
}

.form-group.floating .form-label {
  position: absolute;
  top: 50%;
  left: var(--spacing-4);
  transform: translateY(-50%);
  background: var(--primary-color);
  padding: 0 var(--spacing-2);
  margin: 0;
  pointer-events: none;
  transition: var(--transition-all);
  z-index: 1;
}

.form-group.floating.focused .form-label,
.form-group.floating.has-value .form-label {
  top: 0;
  transform: translateY(-50%);
  font-size: var(--font-size-xs);
  color: var(--accent-color);
}

/* ==========================================================================
   INPUT FIELDS
   ========================================================================== */

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: var(--spacing-4);
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  color: var(--cosmic-white);
  font-size: var(--font-size-base);
  font-family: var(--font-secondary);
  transition: var(--transition-all);
  outline: none;
  min-height: 48px; /* Touch-friendly */
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: var(--cosmic-gray-400);
  opacity: 1;
}

/* Focus states */
.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px rgba(0, 255, 224, 0.2);
  background: rgba(255, 255, 255, 0.08);
}

/* Hover states */
.form-input:hover,
.form-textarea:hover,
.form-select:hover {
  border-color: rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.07);
}

/* ==========================================================================
   TEXTAREA SPECIFIC
   ========================================================================== */

.form-textarea {
  min-height: 120px;
  resize: vertical;
  font-family: var(--font-secondary);
  line-height: var(--line-height-relaxed);
}

/* Character counter */
.character-counter {
  position: absolute;
  bottom: var(--spacing-2);
  right: var(--spacing-3);
  font-size: var(--font-size-xs);
  color: var(--cosmic-gray-400);
  pointer-events: none;
}

.character-counter.warning {
  color: var(--warning-color);
}

.character-counter.error {
  color: var(--error-color);
}

/* ==========================================================================
   VALIDATION STATES
   ========================================================================== */

/* Success state */
.form-group.valid .form-input,
.form-group.valid .form-textarea,
.form-group.valid .form-select {
  border-color: var(--success-color);
  box-shadow: 0 0 0 3px rgba(40, 167, 69, 0.2);
}

.form-group.valid .form-label {
  color: var(--success-color);
}

/* Error state */
.form-group.error .form-input,
.form-group.error .form-textarea,
.form-group.error .form-select {
  border-color: var(--error-color);
  box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.2);
  animation: shake 0.3s ease-in-out;
}

.form-group.error .form-label {
  color: var(--error-color);
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-4px); }
  75% { transform: translateX(4px); }
}

/* ==========================================================================
   ERROR MESSAGES
   ========================================================================== */

.form-error {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  margin-top: var(--spacing-2);
  font-size: var(--font-size-sm);
  color: var(--error-color);
  opacity: 0;
  transform: translateY(-10px);
  transition: var(--transition-all);
}

.form-group.error .form-error {
  opacity: 1;
  transform: translateY(0);
}

.form-error-icon {
  font-size: var(--font-size-xs);
}

/* ==========================================================================
   SUCCESS MESSAGES
   ========================================================================== */

.form-success {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  margin-top: var(--spacing-2);
  font-size: var(--font-size-sm);
  color: var(--success-color);
  opacity: 0;
  transform: translateY(-10px);
  transition: var(--transition-all);
}

.form-group.valid .form-success {
  opacity: 1;
  transform: translateY(0);
}

/* ==========================================================================
   FORM ACTIONS
   ========================================================================== */

.form-actions {
  display: flex;
  gap: var(--spacing-4);
  margin-top: var(--spacing-8);
  justify-content: flex-end;
}

.form-submit {
  position: relative;
  overflow: hidden;
}

.form-submit.loading {
  pointer-events: none;
}

.form-submit.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.form-submit.loading .btn-text {
  opacity: 0;
}

/* ==========================================================================
   FORM STATUS MESSAGES
   ========================================================================== */

.form-status {
  padding: var(--spacing-4);
  border-radius: var(--radius-lg);
  margin-bottom: var(--spacing-6);
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  font-weight: var(--font-weight-medium);
  opacity: 0;
  transform: translateY(-20px);
  transition: var(--transition-all);
}

.form-status.show {
  opacity: 1;
  transform: translateY(0);
}

.form-status.success {
  background: rgba(40, 167, 69, 0.1);
  border: 1px solid var(--success-color);
  color: var(--success-color);
}

.form-status.error {
  background: rgba(220, 53, 69, 0.1);
  border: 1px solid var(--error-color);
  color: var(--error-color);
}

/* ==========================================================================
   PROGRESSIVE DISCLOSURE
   ========================================================================== */

.form-section {
  opacity: 0;
  transform: translateY(20px);
  transition: var(--transition-all);
  margin-bottom: var(--spacing-6);
}

.form-section.visible {
  opacity: 1;
  transform: translateY(0);
}

.form-section-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  margin-bottom: var(--spacing-4);
  padding-bottom: var(--spacing-3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.form-section-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--cosmic-white);
}

.form-section-icon {
  width: 24px;
  height: 24px;
  background: var(--gradient-accent);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-sm);
  color: var(--primary-color);
}

/* ==========================================================================
   RESPONSIVE DESIGN
   ========================================================================== */

@media (max-width: 768px) {
  .form-container {
    padding: var(--spacing-6);
  }
  
  .form-input,
  .form-textarea,
  .form-select {
    padding: var(--spacing-3);
    font-size: 16px; /* Prevent zoom on iOS */
  }
  
  .form-actions {
    flex-direction: column;
    gap: var(--spacing-3);
  }
  
  .form-actions .btn {
    width: 100%;
    justify-content: center;
  }
}

/* ==========================================================================
   ACCESSIBILITY ENHANCEMENTS
   ========================================================================== */

@media (prefers-reduced-motion: reduce) {
  .form-input,
  .form-textarea,
  .form-select,
  .form-error,
  .form-success,
  .form-status,
  .form-section {
    transition: none;
  }
  
  .form-group.error .form-input,
  .form-group.error .form-textarea,
  .form-group.error .form-select {
    animation: none;
  }
  
  .form-submit.loading::after {
    animation: none;
  }
}

@media (prefers-contrast: high) {
  .form-input,
  .form-textarea,
  .form-select {
    background: var(--primary-color);
    border: 2px solid var(--cosmic-white);
  }
  
  .form-input:focus,
  .form-textarea:focus,
  .form-select:focus {
    border-color: var(--accent-color);
    box-shadow: none;
  }
  
  .form-container {
    background: var(--primary-color);
    border: 2px solid var(--cosmic-white);
  }
}

/* ==========================================================================
   CUSTOM FORM ELEMENTS
   ========================================================================== */

/* Custom checkbox */
.form-checkbox {
  position: relative;
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-3);
  cursor: pointer;
  user-select: none;
}

.form-checkbox input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.form-checkbox-mark {
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition-all);
}

.form-checkbox input:checked + .form-checkbox-mark {
  background: var(--accent-color);
  border-color: var(--accent-color);
}

.form-checkbox input:checked + .form-checkbox-mark::after {
  content: '✓';
  color: var(--primary-color);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-bold);
}

.form-checkbox:hover .form-checkbox-mark {
  border-color: var(--accent-color);
}

.form-checkbox input:focus + .form-checkbox-mark {
  box-shadow: 0 0 0 3px rgba(0, 255, 224, 0.2);
}